import React from "react"

import * as introStyles from "./intro.style"

import * as btns from "./../buttons"

const IntroHi: React.FC = () => {
  const colourData = [
    { id: 0, colour: "#F8F8F8", classN: "white-bg" },
    { id: 1, colour: "#4C6CD3", classN: "main-bg" },
    { id: 2, colour: "#87A2F7", classN: "primary-bg" },
    { id: 3, colour: "#2A2A2A", classN: "black-bg" },
  ]

  const coloursList = colourData.map(colour => {
    return (
      <button className={colour.classN} id="colour-btn" key={colour.id}>
        <div className="colour-code">{colour.colour}</div>
      </button>
    )
  })

  if (typeof window !== `undefined`) {
    return (
      <div>
        <div className="styled-triangle primary-bg"></div>
        <introStyles.HiContainer id="hi-container">
          <h5
            className={`white-toggle ${
              localStorage.getItem("dark") == "true" ? "white" : ""
            }`}
            style={{ fontSize: 24 }}
          >
            Hi, my name is
          </h5>
          <h1
            className={`pad-y white-toggle ${
              localStorage.getItem("dark") == "true" ? "white" : ""
            }`}
            style={{ fontSize: 48, fontWeight: 900 }}
          >
            Harsh Singh
          </h1>
          <h1
            className={`white-toggle ${
              localStorage.getItem("dark") == "true" ? "white" : ""
            }`}
            style={{ fontSize: 36 }}
          >
            The web is my playground
          </h1>
          <btns.ScaleButton
            href="#contact"
            style={{ marginTop: 30, fontWeight: 900 }}
          >
            Say Hi 
          </btns.ScaleButton>
          <introStyles.OtherContainerDiv id="hide">
            <introStyles.OtherContainer src="https://cdn.discordapp.com/attachments/769765299599048744/783799407597322260/logo.png" />
            <div className="colour">{coloursList}</div>
          </introStyles.OtherContainerDiv>
        </introStyles.HiContainer>
      </div>
    )
  }
}

export default IntroHi
