import React, { useState } from "react"

const links = [
  {
    href: "#home",
    text: "Home",
  },
  {
    href: "#about",
    text: "About",
  },
  {
    href: "#projects",
    text: "Projects",
  },
  {
    href: "#contact",
    text: "Contact",
  },
  {
    href: "https://dev.to/harshhhdev",
    text: "Blog",
  },
]

const Navbar = () => {
  const [active, setActive] = useState(false)

  let darkMode

  function handleBurgerClick() {
    setActive(prevState => !prevState)
  }

  setMode()

  function setMode() {
    if (typeof window !== `undefined`) {
      if (localStorage.getItem("dark") == "true") {
        darkMode = true
      } else {
        darkMode = false
      }
    }
  }

  function handleModeToggle() {
    if (typeof window !== `undefined`) {
      if (darkMode) {
        const darkenElements = document.getElementsByClassName("dark-toggle")
        const whiteElements = document.getElementsByClassName("white-toggle")
        const dark2 = document.getElementsByClassName("dark2-toggle")

        for (var i = 0; i < darkenElements.length; i++) {
          darkenElements[i].classList.toggle("black-bg")
        }
        for (var i = 0; i < whiteElements.length; i++) {
          whiteElements[i].classList.toggle("white")
        }
        for (var i = 0; i < dark2.length; i++) {
          dark2[i].classList.toggle("light-black-bg")
        }

        darkMode = false
        localStorage.setItem("dark", "false")
      } else {
        const darkenElements = document.getElementsByClassName("dark-toggle")
        const whiteElements = document.getElementsByClassName("white-toggle")
        const dark2 = document.getElementsByClassName("dark2-toggle")

        for (var i = 0; i < darkenElements.length; i++) {
          darkenElements[i].classList.toggle("black-bg")
        }
        for (var i = 0; i < whiteElements.length; i++) {
          whiteElements[i].classList.toggle("white")
        }
        for (var i = 0; i < dark2.length; i++) {
          dark2[i].classList.toggle("light-black-bg")
        }

        darkMode = true
        localStorage.setItem("dark", "true")
      }
    }
  }

  if (typeof window !== `undefined`) {
    return (
      <nav
        className={`dark2-toggle main ${
          localStorage.getItem("dark") == "true" ? "light-black-bg" : ""
        }`}
      >
        <div className="logo">
          <a href="#" className="main">
            <h4
              style={{ fontWeight: 900 }}
              className={`white-toggle ${
                localStorage.getItem("dark") == "true" ? "white" : ""
              }`}
            >
              Harsh Singh
            </h4>
          </a>
        </div>
        <ul
          className={`nav-links ${active ? "nav-active" : ""}`}
          id="nav-links"
        >
          {links.map((link, index) => (
            <li key={index}>
              <a
                className={`white-toggle main ${
                  localStorage.getItem("dark") == "true" ? "white" : ""
                }`}
                href={link.href}
                style={{ fontWeight: "lighter" }}
              >
                {link.text}
              </a>
            </li>
          ))}
          <label className="switch">
            <input
              type="checkbox"
              id="theme-toggle"
              onInput={handleModeToggle}
              defaultChecked={localStorage.getItem("dark") == "true"}
            />
            <span className="slider round"></span>
          </label>
        </ul>
        <div
          className={`burger ${active ? "toggle" : ""}`}
          id="burger"
          onClick={handleBurgerClick}
        >
          <div className="line1 primary-bg"></div>
          <div className="line2 primary-bg"></div>
          <div className="line3 primary-bg"></div>
        </div>
      </nav>
    )
  }
}

export default Navbar
